.cv-block,
.app-content {
  font-family: "Times New Roman", serif;
}
.cv-block h1,
.app-content h1 {
  font-family: "lucida grande", serif;
  font-weight: bold;
  font-size: 2em;
  -webkit-margin-before: 0.5em;
          margin-block-start: 0.5em;
}
.cv-block h2,
.app-content h2 {
  font-weight: bold;
  padding-bottom: 0;
  -webkit-margin-before: 0.8em;
          margin-block-start: 0.8em;
  -webkit-margin-after: 0.1em;
          margin-block-end: 0.1em;
}
.cv-block p,
.app-content p,
.cv-block li,
.app-content li {
  font-size: small;
}
.cv-block p,
.app-content p {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}
.cv-block .ant-divider-horizontal {
  margin: 0 0 10px 0;
}
